<template>
    <span :endTime="endTime">
        <slot>
            {{content}}
        </slot>
    </span>
</template>
<script>
export default {
  data() {
    return {
      content: "",
      timer: ""
    };
  },
  props: {
    endTime: {
      //   type: String,
      default: ""
    }
  },
  mounted() {
    this.countdowm(this.endTime);
  },
  methods: {
    countdowm(val) {
      let self = this;

      var abc = function() {
        var utc = (val - new Date().getTime()) / 1000;
        if (utc <= 0) {
          self.content =
            "剩余: " + "00" + "天" + "00" + "时" + "00" + "分" + "00" + "秒";
          clearInterval(self.timer);
        } else {
          var tian = Math.floor(utc / (24 * 60 * 60));
          var shi = Math.floor((utc / 60 / 60) % 24);
          var fen = Math.floor((utc / 60) % 60);
          var miao = Math.floor(utc % 60);
          if (tian < 10) {
            tian = "0" + tian;
          }
          if (shi < 10) {
            shi = "0" + shi;
          }
          if (fen < 10) {
            fen = "0" + fen;
          }
          if (miao < 10) {
            miao = "0" + miao;
          }
          self.content =
            "剩余: " + tian + "天" + shi + "时" + fen + "分" + miao + "秒";
        }
      };
      abc();
      self.timer = setInterval(function() {
        abc();
      }, 1000);
    }
  }
};
</script>


